import axios from '@/libs/api.request';
import httpClient from './httpClient';
var menu = new Object();

menu.addMenu = function (data) {
  return new Promise(function (resolve, reject) {
    axios.request({
      url: '/menu/add',
      method: 'post',
      data: data
    }).then(function (res) {
      if (res.data.code == 'Success') {
        resolve(res.data);
      } else {
        reject(res.data);
      }
    });
  }); // axios.request({
  //   url: '/menu/add',
  //   method: 'post',
  //   data
  // }).then(res => {
  //   console.log(res)
  //   if (res.data.code != "Success") {
  //     fnError(res.data)
  //   } else {
  //     fnOk(res.data)
  //   }
  // })
};

menu.deleteMenu = function (data, fnOk, fnError) {
  axios.request({
    url: "/menu/delete?id=".concat(data.id),
    method: 'post'
  }).then(function (res) {
    console.log(res);

    if (res.data.code != 'Success') {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

menu.updateMenu = function (data, fnOk, fnError) {
  axios.request({
    url: '/menu/update',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != 'Success') {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

menu.getMenus = function (data, fnOk, fnError) {
  axios.request({
    url: "/menu/list",
    method: 'post'
  }).then(function (res) {
    if (res.data.code != 'Success') {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
}; // 封装一层axios 并返回Promise对象 实现链式调用


menu.getMenuList = function (data) {
  return httpClient.post({
    url: "/menu/list",
    data: data
  });
};

menu.getMenusByParentId = function (data) {
  return httpClient.get({
    url: '/menu/getMenusByParentId',
    data: data
  });
};

menu.getMenusLevel1 = function (data, fnOk, fnError) {
  axios.request({
    url: "/menu/getMenusByParentId?parentId=0",
    method: 'get'
  }).then(function (res) {
    if (res.data.code != 'Success') {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

export default menu;